body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  
}

#root {
  height: 100vh;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.CircleColor {
    margin-top: 5px;
    width: calc(16px + 2vmin);
    height: calc(16px + 2vmin);
    border-radius: 200px 200px 200px 200px;
    -moz-border-radius: 200px 200px 200px 200px;
    -webkit-border-radius: 200px 200px 200px 200px;
    border: 0px solid #000000;
}

.CircleColor:hover {
    border: 1px solid #8f8f8f;
}

.CircleColor:active {
    border: 1px solid #8f8f8f;
}
.Capacity {
    height: 100%;
    width: 100%;
    white-space: nowrap;
}

.CapacityItem {
    font-size: calc(4px + 2vmin);
    width: 100%;
    height: 100%;  
}

.CapacityColumn {
    -webkit-justify-content: center;
            justify-content: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: wrap;
            flex-direction: wrap;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
}

.Phone {
    background-color: rgba(1, 93, 136, 0.411);
    border-radius: 27px 27px 27px 27px;
    -moz-border-radius: 27px 27px 27px 27px;
    -webkit-border-radius: 27px 27px 27px 27px;
    border: 1px solid #7a7a7a;
    width: 40%;
    max-height: 100%;
    height: 25vh;
    margin-top: 5px;
    margin-bottom: 10px;
    box-shadow: inset -81px -77px 189px -45px rgba(0,0,0,0.26);
    -webkit-transition: background-color 0.4s;
    transition: background-color 0.4s;
}

.Price {
    -webkit-justify-content: center;
            justify-content: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: wrap;
            flex-direction: wrap;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
    font-size: calc(16px + 2vmin);
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

.Cellphone {
    background-color: rgb(255, 255, 255);
    height: 5vmin;
    width: 5vmin;
    margin-top: 5px;
    box-shadow: inset 0px 0px 15px 0px rgba(0,0,0,0.4);
    border-radius: 200px 200px 200px 200px;
    -moz-border-radius: 200px 200px 200px 200px;
    -webkit-border-radius: 200px 200px 200px 200px;
    border: 2px solid #2020209f;
}
.CapacityButton {
    font-size: calc(12px + 1vmin);
    margin: 2px;
}
.PriceDetails {
    -webkit-justify-content: center;
            justify-content: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: wrap;
            flex-direction: wrap;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
    font-size: calc(12px + 2vmin);
}

.CurrentPrice {
    font-size: calc(18px + 2vmin);
}

.OldPrice {
    text-decoration: line-through;
}
.DisplayedProduct {
    margin-top: 2em;
    border-radius: 15px;
    border: 2px;
    border-color: black;
    font-size: calc(8px + 2vmin);
    font-family: 'Lato', sans-serif;
    background-color: #f4f2f4;
}

.container {
    width: 100%;
  }

  .Body {
    -webkit-justify-content: center;
            justify-content: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: wrap;
            flex-direction: wrap;
    text-align: center;
  }

  .btn-primary {
    background-color: #f4f2f4;
    border: 0px;
    text-shadow: 0px 0px 11px rgb(0, 0, 0);
    color: black;
    border-radius: 15px;
  }

  .btn-primary:active {
    background-color: #8b8b8b;
    -webkit-transition: none;
    transition: none;
  }

  
  .btn-primary:hover {
    background-color: #8b8b8b;
    -webkit-transition: none;
    transition: none;
    -webkit-transform: none;
            transform: none;
  }

  .sweet-loading {
    margin-top: 50px;
  }
.LayoutRow {
    width: 100%;
    -webkit-justify-content: center;
            justify-content: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: wrap;
            flex-direction: wrap;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
    margin-right: -5px;
    margin-left: -5px;
    padding-left: 7px;
}
